.Loader_loader__3OUZr {
    position: relative;
    display: flex;
  }
  .Loader_loader__3OUZr:before , .Loader_loader__3OUZr:after {
      content: '';
      width: 15px;
      height: 15px;
      display: inline-block;
      position: relative;
      margin: 0  5px  ;
      border-radius: 50%;
      color: rgb(22, 21, 21);
      background: currentColor;
      box-shadow: 50px 0 ,  -50px 0;
      animation: Loader_left__2DswP 1s infinite ease-in-out;
    }
    .Loader_loader__3OUZr:after {
      color: #f5efed;
      animation: Loader_right__1tqCc 1.1s infinite ease-in-out;
    }


  @keyframes Loader_right__1tqCc {
    0% , 100%{transform: translateY(-10px) }
    50% { transform: translateY(10px) }
  }

  @keyframes Loader_left__2DswP {
    0% , 100%{ transform: translateY(10px) }
    50% { transform: translateY(-10px) }
  }

  
  .Loader_roundloader__X-_2Q{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 10px dashed rgba(252, 147, 9, 0.966);
    animation: Loader_rotate__22klb 1s infinite linear;
    margin-bottom: 2em;
}
@keyframes Loader_rotate__22klb {
    from {
        transform: rotate(0deg) scale(1);
    }
    to {
        transform: rotate(360deg) scale(1.4);
    }
}

.Loader_percentloader__3yDl-{
  display: block;
  position: relative;
  /* top: -55%; */
  
  height: 32px;
  width: 200px;
  background: #fff;
  border:2px solid #fff;
  color: rgb(238, 223, 223);
  overflow: hidden;
}
.Loader_percentloader__3yDl-::before{
  content: '';
  background: rgb(31, 29, 29);
  position: absolute;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
  animation: Loader_loading__1mCtF 10s linear infinite;
}
.Loader_percentloader__3yDl-:after{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 24px;
  line-height: 32px;
  color: rgb(0,255,255);
  mix-blend-mode: difference;
  animation: Loader_percentage__jtjKw 10s linear infinite;
}

@keyframes Loader_loading__1mCtF {
  0% { width: 0 }
  100% { width: 100% }
}
