  .mediaSong::-webkit-scrollbar {
    width: 0.1px;
  }
  
  .mediaImage_modal {
    max-width: none;
    min-width: auto;
    min-height: auto;
    padding: 1.5rem;
    height: 53%;
    width: auto;
  }
  
  .mediaImage_modal > img {
    /* display: block;
    width: 100%;
    height: 87px;
    object-fit: cover; */
    display: block;
    width: 121%;
    height: 108px;
    object-fit: cover;
    border-radius: 50%;
    box-shadow: var(--boxShadow);
  }
  
  .headerSong {
    text-align: right;
    font-size: 1.5rem;
    clip-path: polygon(13.37% 10.82%,50.56% 0.00%,72.41% 5.48%,97.76% 0.88%,91.94% 41.55%,96.97% 66.64%,86.31% 94.51%,50.00% 100.00%,17.86% 100.00%,2.35% 77.37%,0.98% 37.80%);
    padding: 0.2rem 1.3rem;
    color: whitesmoke;
    background-color: var(--headerSing);
    border-radius: 0.5rem;
    /* border: 2px solid var(--titleSongColor); */
    /* border-bottom: 4px solid #b2b1b1; */
    border: 1px solid white;
    text-shadow: -1px -1.6px 0px #151616;
    box-shadow: var(--boxShadow);
    cursor: pointer;
    padding: 0 1em 0 1em;
  }
  
  .headerSong p {
    margin: 0%;
    /* padding: 0 1em 0 1em; */
  }
  
  .linkTo {
    position: absolute;
    top: 97%;
    left: 31%;
    background-color: rgb(211 24 24);
    border: none;
    color: white;
    padding: 2px 6px;
    font-size: 14px;
    cursor: pointer;
    border-radius: .5em;
    z-index: 7;
  }
  
  .audioBlock {
    display: block;
  }
  
  .audioBlock p {
    font-size: 1.5rem;
    text-align: center;
    color: var(--colorText);
    /* padding: 2px 6px; */
    /* padding: 0 1em 0 1em; */
    /* margin: 5em; */
    /* background-color: var(--tributeAfterBefore); */
    background-color: var(--colorNameSongs);
    border-radius: 0.5em;
    text-shadow: var(--textShadow);
    box-shadow: var(--boxShadow);
    border: 1px solid white;
  margin: 1em;
  line-height: 1.75;
  /* -webkit-clip-path:var(--clipPath); */
  /* clip-path: var(--clipPath); */
}
  
  .videoBlock p {
    /* display: block; */
    font-size: 1.5rem;
    text-align: center;
    color: var(--colorText);
    background-color: var(--colorNameSongs);
    border-radius: 0.5em;
    border: 1px solid white;
    text-shadow: var(--textShadow);
    box-shadow: var(--boxShadow);
    margin: 1em;
    line-height: 1.75;
    /* -webkit-clip-path:var(--clipPath); */
    /* clip-path: var(--clipPath); */
  }
  .videoBlock {
    position: relative;
    width: 100%;
    /* height: 255px; */
  }
  
  #videoFrame {
    position: relative;
  
    width: 100% !important;
    height: 22vh !important;
    border-width: 0;
    outline-width: 0;
  }
  
  audio::-webkit-media-controls-panel {background-color: #e6cdcd;}
  audio::-webkit-media-controls-play-button {background-color: rgb(255, 102, 0);}
  audio::-webkit-media-controls-volume-slider {background-color: #ccc;}
  
  
  
  .tziTata {
    /* position:sticky; */
    font-size: 0.5em;
    /* padding: 0.5em;  */
    /* height: 76rem; */
    margin-top: 10px;
    font-size: 2.5em;
    text-align: center;
    background-color: #44444473;
    text-shadow: var(--textShadow);
    /* box-shadow: var(--boxShadow); */
    border-radius: 1em;
    color: var(--colorText);
    border-color: #2196f3;
    /* border: 2px dotted #e4e0e7; */
  
  
    /* opacity:70% */
  }
  
  .tziTata p {
    font-size: 0.5em;
    color: #eee7de;
    background-color: var(--buttonBack);
    text-shadow: var(--textShadow);
    /* border: 3px  solid 2196f3;    */
  }
  .tziTata img {
   
    height: auto;
    width: 100%;
    display: flex;
    border-radius: 2%;
    align-items: center;
  
  }
  
  .bTnSing {
    position: absolute;
    top: 1%;
    left: 78%;
    /* background-color: #bf6947ba; */
    /* border-radius: 6%;
    padding: 0.2em;
    opacity: 0.5; */
    border-radius: 0.5em;
    padding: 0.5em;
    color: white;
    border: 1px solid #eeeeee;
    background-color: var(--buttonBack);
    cursor: pointer
  }
  
  .btnError {
    border-radius: 0.5em;
    padding: 0.5em;
    color: black;
    border: 1px solid #eeeeee;
    background-color: whitesmoke;
  }